*
{
    margin:0;
    padding:0;
}
body {
    background: url("bg.avif");background-repeat: no-repeat;background-size: cover;background-position: center;
    font-family: "Bree Serif", serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
  }
  .heading
  {
    text-align: center;
    color:yellow;
    font-size: 3rem;margin-bottom: 15px;
  
  }
  .app
  {
    background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(8,50,99,1) 100%);
    min-width: 600px;
    min-height: 200px;
    height: min-content;
    border-radius: 15px;
    padding:30px;
    box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
    /* display: flex;
    justify-content: space-evenly; */
  }

.Question-section
{
position: relative;
width: 100%;
}
.Question-count
{
    color:yellowgreen;
    font-size:20px;
    margin-bottom: 20px;
}
.Question-count span
{
  font-size: 40px;
  color: yellow;
}
.score-section span
{
    font-size:30px;
    color:chartreuse;
    font-weight:bolder;
    font-family: 'Courier New', Courier, monospace;

}
.Question-text
{
    color:azure;
    font-size: 30px;
  font-weight: bold;margin-bottom: 15px;

}
.Answer-section
{
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: space-between;
}
button {
    width:350px;
    font-size: 16px;
    color:#1b295c;
    background:#D4FFE3;
    border-radius: 15px;
    display: flex;
    padding:10px;
    justify-content:center;
    align-items: center;
    border: 3px solid #234668;
    cursor: pointer;margin-bottom: 10px;
  }
  
  button:hover {
    background-color:blanchedalmond;color:green;
  }
  .score-section{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  font-size: 30px;
  color:white;
  font-weight: bold;
  min-height: 280px;
  }
  .score-section p
  {
    margin-bottom: 15px;
  }
  .score-section small
  {
    font-size:16px;
    margin-top: 50px;
    color:yellow;display: flex;align-self: flex-start;
  }